import React, {useState} from 'react';

import {Card, CardContent, CardHeader, Divider, Grid, TextField} from '@material-ui/core';

const states = [
  {
    value: 'alabama',
    label: 'Alabama'
  },
  {
    value: 'new-york',
    label: 'New York'
  },
  {
    value: 'san-francisco',
    label: 'San Francisco'
  }
];

function Stats(props) {
  const profile=props




  return (
      <form
          autoComplete="off"
          noValidate
          {...props}
      >
        <Card>
          <CardHeader
              subheader="Estadísticas del usuario"
              title="Estadísticas"
          />
          <Divider/>
          <CardContent>
            <Grid
                container
                spacing={3}
            >
              <Grid
                  item
                  md={6}
                  xs={12}
              >
                <TextField
                    fullWidth
                    label="Partidas jugadas"
                    name="firstName"
                    defaultValue={'0'}
                    value={profile.jugadas}
                    variant="outlined"
                    disabled={true}
                />
              </Grid>

              <Grid
                  item
                  md={6}
                  xs={12}
              >
                <TextField
                    fullWidth
                    label="Partidas ganadas"
                    name="email"
                    defaultValue={'0'}
                    value={profile.ganadas}
                    variant="outlined"
                    disabled={true}
                />
              </Grid>
              <Grid
                  item
                  md={6}
                  xs={12}
              >
                <TextField
                    fullWidth
                    label="Monedas obtenidas"
                    name="question"
                    value={profile.coins}
                    defaultValue={'0'}
                    variant="outlined"
                    disabled={true}
                />
              </Grid>

            </Grid>
          </CardContent>
          <Divider/>


        </Card>


      </form>

  );
}

export default Stats;
